import React ,{useState} from 'react';
import { makeStyles } from '@material-ui/core/styles';
import { Link } from "react-router-dom";
import MobileRightMenuSlider  from "@material-ui/core/Drawer"
import {
    AppBar,
    Toolbar,
    ListItem,
    IconButton,
    ListItemText,
    Avatar,
    Divider,
    List,
    Typography,
    Box,
    ListItemIcon

} from "@material-ui/core";
import avatar from "../avatar.png"
import {
    Menu,
    AssignmentInd,
    Home,
    Apps,
    ContactMail
} from "@material-ui/icons"

const useStyles=makeStyles(theme=>({
    menuSliderContainer:{
        width:250,
        background:"#521",
        height:"100%"
    },
    avatar:{
        display:"block",
        margin:"0.5rem auto",
        width:theme.spacing(13),
        height:theme.spacing(13)
    },
    listItem:{
        color:"tan"
    }
}))


const menuItems=[
    {
        listIcon:<Home />,
        listText:"Home",
        linkto:"/"

    },
    {
        listIcon:<AssignmentInd />,
        listText:"Resume",
        linkto:"/resume"
    },
    {
        listIcon:<Apps />,
        listText:"Portfolio",
        linkto:"/portfolio"
    },
    {
        listIcon:<ContactMail />,
        listText:"Contacts",
        linkto:"/contact"
    }
]
const Navbar = () => {
    const [state, setstate] = useState({
        right:false
    })

    const toggleSlider=(slider,open)=>()=>{
        setstate({...state,[slider]:open})
    }
    const classes=useStyles();

    const sideList=slider=>(
        <Box className={classes.menuSliderContainer} onClick={toggleSlider("right",false)} component="div">
            <Avatar className={classes.avatar} src={avatar} alt="Salman Iqbal"/>
            <Divider />
            <List>
                {menuItems.map((lstItm,key)=>(

                <ListItem  button key={key} component={Link} to={lstItm.linkto}>
                    <ListItemIcon className={classes.listItem}>
                        {lstItm.listIcon}
                    </ListItemIcon>
                    <ListItemText className={classes.listItem} primary={lstItm.listText} />
                </ListItem>
                ))}
            </List>
        </Box>
    )


    return (
        <>
        
       <Box component="nav">
           <AppBar position="static" style={{background:"#222"}}>
               <Toolbar>
                 <IconButton onClick={toggleSlider("right",true)}>
                    <Menu style={{color:"tomato"}} />
                 </IconButton>
                 <Typography variant="h5" style={{color:"tan"}}>
                     Portfolio
                 </Typography>
                 <MobileRightMenuSlider 
                 anchor="left" 
                 open={state.right}
                 onClose={toggleSlider("right",false)}
                 >
                     {sideList("right")}
                 </MobileRightMenuSlider>
               </Toolbar>
           </AppBar>

       </Box>
       </>
    )
}

export default Navbar
