import React from 'react'
import Navbar from "./Navbar"
import { makeStyles } from '@material-ui/core/styles';
import {
    Card,
    CardMedia,
    CardContent,
    Typography,
    Box,
    Grid,
    CardActionArea,
    Button,
    CardActions

} from "@material-ui/core";

import project3 from "../images/konnect.png"
import project1 from "../images/react.png"
import project2 from "../images/html-css-javascript.jpg"
import project4 from "../images/mern.png"



const useStyles=makeStyles({
    mainContainer:{
        background:"#233",
        height:"100%"
    },
    cardContainer:{
        maxWidth:345,
        margun:"3rem",
        margin:"5rem auto"
    }
})
const Portfolio = () => {
    const classes=useStyles();
    return (
       <Box component="div">
        <Navbar />
        <Grid container justify="center" alignItems="center" className={classes.mainContainer}>
            {/*P1 */}
            <Grid item xs={12} sm={8} md={6}> 
                <Card  className={classes.cardContainer}>
                    <CardActionArea>
                        <CardMedia 
                        component="img"
                        alt="project 1"
                        height="140"
                        image={project1} />

                        <CardContent >
                            <Typography gutterBottom variant="h5">
                               Covid-19 Tracker App
                            </Typography>
                            <Typography variant="body2" color="textSecondary" component="p">
                                Covid-19 Tracker App is the App to Track the Number of Corona Cases in the World.
                                It also tell's the Number of Recovered and Death's
                            </Typography>
                        </CardContent>
                        <CardActions>
                            
                            <Button size="small" target="_Blanck" href="https://covid19-trackr.herokuapp.com/" color="primary">
                                Live Demo
                            </Button>
                        </CardActions>
                    </CardActionArea>
                </Card>
            </Grid>

            <Grid item xs={12} sm={8} md={6}> 
                <Card  className={classes.cardContainer}>
                    <CardActionArea>
                        <CardMedia 
                        component="img"
                        alt="project 1"
                        height="140"
                        image={project2} />

                        <CardContent >
                            <Typography gutterBottom variant="h5">
                                To-Do App
                            </Typography>
                            <Typography variant="body2" color="textSecondary" component="p">
                                With This App You Can Create Your Daily Task,
                                You Can Also mark Them as Completed/Uncompleted 
                                And You Can also Delete the Task.

                                This App Use the local Storage So You Cannot lose your Daile Task on Page referesh .
                            </Typography>
                        </CardContent>
                        <CardActions>
                            
                            <Button size="small" color="primary" target="_Blank" href="https://react-todoap.herokuapp.com/">
                                Live Demo
                            </Button>
                        </CardActions>
                    </CardActionArea>
                </Card>
            </Grid>
            <Grid item xs={12} sm={8} md={6}> 
                <Card  className={classes.cardContainer}>
                    <CardActionArea>
                        <CardMedia 
                        component="img"
                        alt="project 1"
                        height="140"
                        image={project4} />

                        <CardContent >
                            <Typography gutterBottom variant="h5">
                                Url Shortner
                            </Typography>
                            <Typography variant="body2" color="textSecondary" component="p">
                                This is The Url Shortner App Through Whick You Can Shorten Any Longer Url So you Don't have to Worry About Keeping Long Url in mind.
                            </Typography>
                        </CardContent>
                        <CardActions>
                            
                            <Button size="small" color="primary" target="_Blank" href="https://url-shrtner.herokuapp.com/">
                                Live Demo
                            </Button>
                        </CardActions>
                    </CardActionArea>
                </Card>
            </Grid>
           
            <Grid item xs={12} sm={8} md={6} > 
                <Card  className={classes.cardContainer} >
                    <CardActionArea>
                        <CardMedia 
                        component="img"
                        alt="project 1"
                        height="140"
                        image={project3} />

                        <CardContent >
                            <Typography gutterBottom variant="h5">
                                Konnect
                            </Typography>
                            <Typography variant="body2" color="textSecondary" component="p">
                               This is the Simple Social Media App Like Instagram .
                               Where user Can Post New Picture And Like And Comment on other's Pictures.
                         
                            </Typography>
                        </CardContent>
                        <CardActions>
                           
                            <Button size="small"target="_blank" href="https://play.google.com/store/apps/details?id=com.Konnect.App" color="primary">
                                Live Demo
                            </Button>
                        </CardActions>
                    </CardActionArea>
                </Card>
            </Grid>
        </Grid>
       </Box>
    )
}

export default Portfolio
