import React ,{useState} from 'react'
import Navbar from "./Navbar"
import { makeStyles } from '@material-ui/core/styles';
import Notifications, {notify} from 'react-notify-toast';
import {
   
    Typography,
    Box,
    Grid,
    Button,
    TextField

} from "@material-ui/core";
import Alert from '@material-ui/lab/Alert';
import SendIcon from "@material-ui/icons/Send"
import { withStyles } from '@material-ui/styles';




const InputField=withStyles({
    root:{
        "& label.Mui-focused":{
            color:"tomato",
        },
        "& label" :{
            color:"tan"
        },
        "& .MuiOutlinedInput-root":{
            "& fieldset":{
                borderColor:"tan"
            },
            "&:hover fieldset":{
                borderColor:"tan"
            },
            "& .Mui-focused fieldset":{
                borderColor:"tan"
            }
        }
    }
})(TextField)
const useStyles=makeStyles((theme)=>({

    form:{
        top:"50%",
        left:"50%",
        transform:"translate(-50%, -50%)",
        position:"absolute"
    },
    button:{
        borderColor:"tomato",
        color:"tan"
    }
}));


const Contact = () => {


const [Email, setEmail] = useState("");
const [Name, setName] = useState("");
const [Company, setCompany] = useState("");
const emailChangeHandler=(e)=>{
   setEmail(e.target.value);
}
const nameChangeHandler=(e)=>{
    setName(e.target.value);
 }

 const companyChangeHandler=(e)=>{
    setCompany(e.target.value);
 }
 const [Status, setStatus] = useState("Not Ok")

const asd=async(e)=>{
    e.preventDefault();
    const apires= await apiCall();
    console.log(apires);
    if(apires=="ok"){
        let myColor = { background: '#123456', text: "#FFFFFF" };
        notify.show('Message Received. Will Contact you back In SomeTime!',"custom", 5000, myColor);
    }
  
    
}
const apiCall=async()=>{
    const requestOptions = {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({Name:Name,Email:Email,Company:Company})
        };
       let data= await fetch("https://getform.io/f/5313cb35-d668-411f-9d4b-c85b017e579f",requestOptions)
        .then(response=>response);

        console.log(data.status)
        {if(data.status===200){
           
            return "ok"}
            else {return "Not Ok"}
        }
}
    const classes=useStyles();
    return (
        <Box component="div">
            
        <Navbar />


        <Grid container justify="center">
            <Box component="form" className={classes.form} onSubmit={asd} >
                <Typography variant="h5">
                    Hire or Contact Me........
                </Typography>
               
                <InputField onChange={nameChangeHandler} fullWidth={true} name="Name" label="Name" inputProps={{style:{color:"white"}}} variant="outlined"  margin="dense" size="medium"/>
               
                <InputField onChange={emailChangeHandler} fullWidth={true}  name="Email" label="Email" inputProps={{style:{color:"white"}}} variant="outlined" margin="dense" size="medium" />
                <InputField onChange={companyChangeHandler} fullWidth={true}  name="Company" label="Company Name" inputProps={{style:{color:"whitesmoke"}}} variant="outlined" margin="dense" size="medium" />
                <Button className={classes.button}  type="submit"  variant="outlined" fullWidth={true} endIcon={<SendIcon />} >
                    Contact Me
                </Button>
               
               {Status ==="ok" ?   <Alert severity="success" style={{marginTop:"10px"}}> </Alert>: null }
            </Box>
        </Grid>
        
        <Notifications />
        </Box>
    )
}

export default Contact;
