import React from 'react'
import {Typography,Avatar,Grid,Box} from "@material-ui/core"
import avatar from "../avatar.png"
import Typed from "react-typed"
import {makeStyles} from '@material-ui/core/styles'

const useStyles=makeStyles(theme=>({
    avatar:{
        width:theme.spacing(15),
        height:theme.spacing(15),
        margin:theme.spacing(1)
    },
    title:{
        fontSize:"2rem",
        color:"tomato"
    },
    subtitle:{
        color:"tan",
        marginBottom:"3rem",
        fontSize:"1rem"
    },
    typedContainer:{
        position:"absolute",
        top:"50%",
        left:"50%",
        width:"100vw",
        textAlign:"center",
        transform:"translate(-50%,-50%)",
        zIndex:1


    }
}))

const Header = () => {
    const classes= useStyles();

    return (
        <Box className={classes.typedContainer}>
            <Grid container justify="center">
            <Avatar className={classes.avatar} src={avatar} alt="Salman Iqbal" />
            </Grid>
            <Typography varient="h4" className={classes.title}>
                <Typed strings={["Salman Iqbal"]} backSpeed={60} loop typeSpeed={40} />
            </Typography>
            <br/>

            <Typography varient="h5" className={classes.subtitle}>
                <Typed strings={["Web Design" , "Web Development" , "Mern Stack"]}
                 typeSpeed={40}
                 backSpeed={60}
                 loop />
            </Typography>
            
        </Box>
    )
}

export default Header
